<template>

    <Modal
            :styles="{top: '20px',height: '90%'}"
            v-model="showDlg"
            :title="title"
            :mask-closable="false"
            :closable="false"
            >
        <template v-if="isLoaded == false">
            <Spin fix>
                <Icon type="ios-loading" size=24 class="demo-spin-icon-load"></Icon>
                <div>Loading</div>
            </Spin>
        </template>
        <template v-else>
            <tree-table
                    ref="treeModal"
                    :data="treeData"
                    :columns="columns"
                    :stripe="true"
                    :tree-type="true"
                    :is-fold="isTreeFold"
                    :expand-type="false"
                    :selectable="true"
            >
            </tree-table>
        </template>

        <div slot="footer">
            <Row>
                <Col span="12" style="text-align: left">
                    <i-switch size="large"  v-model="isTreeFold"  :true-value="true" :false-value="false" >
                        <span slot="open">折叠</span>
                        <span slot="close">展开</span>
                    </i-switch>
                </Col>
                <Col span="12">
                    <Button type="text"  @click="cancel">取消</Button>
                    <Button type="primary"  @click="ok">保存</Button>
                </Col>
            </Row>
        </div>

    </Modal>

</template>

<script>
    import Vue from 'vue'
    import TreeTable from 'tree-table-vue'

    Vue.use(TreeTable)

    export default {
        name: "tree_modal",
        props:[
            'showDlg',
            'title',
            'isLoaded',
            'treeData',
            'columns',
            'isFold',
            'refTreeName'
        ],
        data(){
          return {
              isTreeFold: true
          }
        },
        mounted(){
            this.isTreeFold = this.isFold
        },
        methods: {
            ok: function () {
                this.$emit('on-ok');
            },
            cancel: function () {
                this.$emit('on-cancel');
            }

        }
    }
</script>

<style scoped>

</style>
